@charset "UTF-8";
html{
  box-sizing:border-box;
}

*,
*::before,
*::after{
  box-sizing:inherit;
}

body{
  font-size:14px;
  font-weight:400;
  letter-spacing:0;
  line-height:1.28581;
  text-transform:none;
  color:#1c2127;
  font-family:-apple-system, "BlinkMacSystemFont", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Open Sans", "Helvetica Neue", "blueprint-icons-16", sans-serif;
}

p{
  margin-bottom:10px;
  margin-top:0;
}

small{
  font-size:12px;
}

strong{
  font-weight:600;
}

::-moz-selection{
  background:rgba(125, 188, 255, 0.6);
}

::selection{
  background:rgba(125, 188, 255, 0.6);
}
.bp6-icon{
  display:inline-block;
  flex:0 0 auto;
  vertical-align:text-bottom;
}
.bp6-icon:not(:empty)::before{
  content:"" !important;
  content:unset !important;
}
.bp6-icon > svg{
  display:block;
}
.bp6-icon > svg:not([fill]){
  fill:currentcolor;
}
.bp6-icon.bp6-icon-muted svg{
  fill-opacity:0.15;
  overflow:visible;
}
.bp6-icon.bp6-icon-muted svg path{
  stroke:#8f99a8;
  stroke-opacity:0.5;
  stroke-width:0.5px;
}
.bp6-dark .bp6-icon .bp6-icon-muted svg{
  fill-opacity:0.2;
}

span.bp6-icon-standard{
  font-family:"blueprint-icons-16", sans-serif;
  font-size:16px;
  font-style:normal;
  font-variant:normal;
  font-weight:400;
  height:16px;
  line-height:1;
  width:16px;
  -moz-osx-font-smoothing:grayscale;
  -webkit-font-smoothing:antialiased;
  display:inline-block;
}

span.bp6-icon-large{
  font-family:"blueprint-icons-20", sans-serif;
  font-size:20px;
  font-style:normal;
  font-variant:normal;
  font-weight:400;
  height:20px;
  line-height:1;
  width:20px;
  -moz-osx-font-smoothing:grayscale;
  -webkit-font-smoothing:antialiased;
  display:inline-block;
}

span.bp6-icon:empty{
  font-family:"blueprint-icons-20";
  font-size:inherit;
  font-style:normal;
  font-weight:400;
  line-height:1;
}
span.bp6-icon:empty::before{
  -moz-osx-font-smoothing:grayscale;
  -webkit-font-smoothing:antialiased;
}
span.bp6-icon:empty.bp6-icon-standard{
  font-size:16px;
}
span.bp6-icon:empty.bp6-icon-large{
  font-size:20px;
}

